import Vue from 'vue';
import VLayout from '@/layouts/default.vue';
import Simplert from 'vue2-simplert'
import ClientsService from '@/services/clients';
import { mapState } from 'vuex';
import moment from 'moment';
import Pagination from '@/components/Pagination.vue';

export default {
  name: 'Clients-page',
  data() {
    return {
      editId: null,
      editRow: null,
    };
  },
  components: {
    VLayout,
    Pagination,
  },
  computed: {
    ...mapState({
      clients: state => state.clients.list,
    }),
  },
  methods: {
    deleteClient(client) {
      ClientsService.deleteClient(client.id).then((res) => {
        console.log(res);
        if (res.data.success) {
          Vue.set(client, 'is_deleted', true);
        } else {
          alert('Ошибка удаления');
        }
      }).catch(() => {
        alert('Ошибка удаления');
      });
    },
    editClient(client) {
      this.editRow = Object.assign({}, client);
      this.editId = client.id;
    },
    saveClient(client) {
      ClientsService.updateClient(this.editRow).then(() => {
        this.editId = null;
        ClientsService.getClients();
      });
    },
    cancelEdit() {
      this.editId = null;
    },
  },
  watch: {
    $route(newRoute, oldRoute) {
      if (newRoute.path === oldRoute.path) {
        if (newRoute.fullPath !== oldRoute.fullPath) {
          ClientsService.getClients({
            params: this.$route.query,
          });
        }
      }
    },
  },
};



