import Vue from 'vue';
import VLayout from '@/layouts/default.vue';
import Simplert from 'vue2-simplert'
import PosService from '@/services/pos';
import { mapState } from 'vuex';
import moment from 'moment';
import Datepicker from 'vuejs-datepicker';

export default {
  name: 'Sell-history-page',
  data() {
    return {
      startDate: new Date(),
      endDate: new Date(),
    };
  },
  components: {
    VLayout,
    Datepicker,
  },
  computed: {
    ...mapState({
      orders: state => state.pos.orders,
    }),
    sum_grouped() {
      const  sums = {};
      if (this.orders && this.orders.length) {
        this.orders.forEach(order => {
          if (order.lines.data.forEach(line => {
              if (!sums.hasOwnProperty(line.category.name)) {
                sums[line.category.name] = 0;
              }
              sums[line.category.name] += line.qty * line.product.price;
            }));
        });
      }
      return sums;
    },
    sum() {
      if (this.orders && this.orders.length) {
        let sum = 0;
        sum = this.orders.reduce((sum, b) => {
          return sum + parseInt(b.total, 10);
        }, sum);
        return sum;
      }
      return 0;
    },
  },
  methods: {
    deleteOrder(order) {
      PosService.deleteOrder(order).then((res) => {
        console.log(res);
        if (res.data.success) {
          order.is_deleted = true;
        } else {
          alert('Ошибка удаления');
        }
      }).catch(() => {
        alert('Ошибка удаления');
      });
    },
    selectStartDate(date) {
      PosService.getOrders({ filters: { startDate: moment(date).format('YYYY-MM-DD'), endDate: moment(this.endDate).format('YYYY-MM-DD') }});
    },
    selectEndDate(date) {
      PosService.getOrders({ filters: { startDate: moment(this.startDate).format('YYYY-MM-DD'), endDate: moment(date).format('YYYY-MM-DD') }});
    },
  }
};



